import Vue from 'vue'
import VueI18n from 'vue-i18n'
import Router from 'vue-router'
import locales from '../i18n/locales'

Vue.use(Router)
Vue.use(VueI18n)

let navLang = navigator.language || navigator.userLanguage
if (navLang === 'zh-CN' || navLang === 'zh-cn') {
  Vue.config.lang = 'cn'
} else if (navLang === 'en-US' || navLang === 'en-us') {
  Vue.config.lang = 'en'
} else {
  Vue.config.lang = 'cn'
}

Object.keys(locales).forEach(lang => {
  Vue.locale(lang, locales[lang])
})

export default new Router({
  routes: [
    {
      name: 'login',
      path: '/login',
      component: function (resolve) {
        require(['../pages/Login.vue'], resolve)
      },
      meta: {
        auth: false,
        title: Vue.t('nav.login'),
        hideNav: true
      }
    },
    {
      path: '/',
      redirect: '/chatroom-setting',
      name: 'overview',
      component: function (resolve) {
        require(['../pages/Overview.vue'], resolve)
      },
      meta: {
        auth: true,
        title: Vue.t('nav.overview'),
        group: 'overview'
      }
    },
    {
      path: '/latest-announcement',
      name: 'latest-announcement',
      component: function (resolve) {
        require(['../pages/SettingAnnouncement.vue'], resolve)
      },
      meta: {
        auth: true,
        title: Vue.t('nav.latest_announcement'),
        group: 'system-setting'
      }
    },
    {
      path: '/chat-record',
      name: 'chat-record',
      component: function (resolve) {
        require(['../pages/SettingChatRecord.vue'], resolve)
      },
      meta: {
        auth: true,
        title: Vue.t('nav.chat_record'),
        group: 'system-setting'
      }
    },
    {
      path: '/chatroom-setting',
      name: 'chatroom-setting',
      component: function (resolve) {
        require(['../pages/ChatroomSetting.vue'], resolve)
      },
      meta: {
        auth: true,
        title: Vue.t('nav.chatroom_setting'),
        group: 'chatroom-setting'
      }
    },
    {
      path: '/member-list',
      name: 'member-list',
      component: function (resolve) {
        require(['../pages/UserMemberList.vue'], resolve)
      },
      meta: {
        auth: true,
        title: Vue.t('nav.member_list'),
        group: 'user-management'
      }
    },
    {
      path: '/agent-promo',
      name: 'agent-promo',
      component: function (resolve) {
        require(['../pages/UserAgentPromo.vue'], resolve)
      },
      meta: {
        auth: true,
        title: Vue.t('nav.agent_promo'),
        group: 'user-management'
      }
    },
    {
      path: '/robot-management',
      name: 'robot-management',
      component: function (resolve) {
        require(['../pages/UserRobotManagement.vue'], resolve)
      },
      meta: {
        auth: true,
        title: Vue.t('nav.robot_management'),
        group: 'user-management'
      }
    },
    {
      path: '/error/',
      name: 'error',
      component: function (resolve) {
        require(['../pages/Error.vue'], resolve)
      },
      meta: {
        auth: false,
        hideNav: true
      }
    }
  ]
})



