'use strict'

// import { locale } from 'moment'
const config = require('../config')
const env = process.env.NODE_ENV === 'development' ? config.dev.env : config.build.env
const host = env.HOST.replace(/"/g, '')
// const localHost = env.LOCALHOST && env.LOCALHOST.replace(/"/g, '')
const prefix = host + '/manage/'
const managePrefix = host + '/v1/manage/'
// const memberPrefix = host + '/v1/member/'
export default {
  my: managePrefix + 'my/',
  login: prefix + 'login/',
  refresh_token: host + '/token/refresh/',
  logout: host + '/logout/',
  user: host + '/v1/user/',
  manageUser: managePrefix + 'user/',
  announcement: managePrefix + 'announcement/',
  message: managePrefix + 'message/',
  chatroomSetting: managePrefix + 'global-preferences/',
  robot: managePrefix + 'robot/'
}



