<template>
  <div class="app">
    <side-bar/>
    <div id="content" class="app-content box-shadow-z1" role="main">
      <nav-bar/>
      <div ui-view class="app-body m-a" id="view">
        <router-view/>
        <notifications/>
      </div>
    </div>
  </div>
</template>
<script>
import $ from './utils/util'
import api from './api'
import axios from 'axios'
import Style from './components/Style.vue'
import SideBar from './components/SideBar.vue'
import NavBar from './components/NavBar.vue'
export default {
  data () {
    return {
      username: '',
      headerDropdown: false
    }
  },
  created () {
    this.getMy()
  },
  methods: {
    refresh () {
      let refreshToken = this.$cookie.get('refresh_token')
      if (!refreshToken) {
        return
      }
      this.$http.post(api.refresh_token, {
        refresh_token: this.$cookie.get('refresh_token')
      }).then(data => {
        let d = new Date(data.expires_in)
        // use access_token to access APIs
        this.$cookie.set('access_token', data.access_token, d.toGMTString(), '/')
        // use refresh_token to fetch new access_token
        this.$cookie.set('refresh_token', data.refresh_token, d.toGMTString(), '/')
        axios.defaults.headers.common['Authorization'] = `${data.token_type} ${data.access_token}`
      }, () => {
        this.toLoginPage()
      })
    },
    setUpRouterHooks () {
      this.$router.beforeEach((to, from, next) => {
        this.headerDropdown = false
        next()
      })
    },
    setUpAuth () {
      let refreshTokenInterval
      // refresh access token every 5 mins
      $.setIndicator(() => {
        refreshTokenInterval = setInterval(() => {
          this.refresh()
        }, 5 * 60 * 1000)
      }, () => {
        clearInterval(refreshTokenInterval)
      })
    },
    getMy () {
      if (!this.$cookie.get('access_token')) {
        this.toLoginPage()
      } else {
        this.$http.get(api.my).then(data => {
          if (data) {
            this.username = data.username
            this.setUpAuth()
            this.setUpRouterHooks()
          } else {
            this.toLoginPage()
          }
        }, () => {
          this.toLoginPage()
        })
      }
    },
    toLoginPage () {
      this.$cookie.delete('access_token')
      this.$cookie.delete('refresh_token')
      this.$router.push({
        path: '/login',
        query: {
          next: this.$route.fullPath
        }
      })
    }
  },
  components: {
    Style,
    SideBar,
    NavBar
  }
}
</script>



