<template>
  <div id="aside" class="app-aside modal fade nav-expand" v-if="!$route.meta.hideNav">
    <div class="left navside black dk" layout="column">
      <div class="navbar no-radius">
        <!-- <router-link class="navbar-brand" to="/"> -->
        <div class="navbar-brand">
          <img src="../assets/logo.png"/>
          <span class="hidden-folded inline m-t-xs">Chamois</span>
        </div>
        <!-- </router-link> -->
      </div>

      <div>
        <nav class="nav-active-blue">
          <ul class="nav" ui-nav="" v-for="(navGroup, index) in sideBarStore" :key="'n_' + index">
            <li :key="navGroup.name + index"><div class="b-b b m-t-sm m-b-sm"></div></li>
            <li
              :key="index"
              :class="[{
                  'active': navGroup.showSubNav
              }]"
            >
              <router-link
                :to="navGroup.path"
                v-if="navGroup.path"
                :class="{'text-blue': $route.meta.group === navGroup.name}"
                style="text-decoration: none;"
              >
                <span class="nav-icon"><i class="m-r-xs" :class="navGroup.icon"></i></span>
                <span class="nav-text">{{ navGroup.displayName }}</span>
              </router-link>
              <a
                @click="navGroup.showSubNav = !navGroup.showSubNav"
                :class="{'text-blue': $route.meta.group === navGroup.name}"
                style="text-decoration: none;"
                v-else
              >
                <span class="nav-caret"><i class="fa fa-caret-down"></i></span>
                <span class="nav-icon"><i class="m-r-xs" :class="navGroup.icon"></i></span>
                <span class="nav-text">{{ navGroup.displayName }}</span>
              </a>
              <ul class="nav-sub" v-if="navGroup.showSubNav">
                <li v-for="(subNav, index) in navGroup.subNav" :key="navGroup.name + index">
                  <router-link :to="subNav.path">
                    <span class="nav-text" :class="{'text-blue': $route.name === subNav.name}">{{ subNav.displayName }}</span>
                  </router-link>
                </li>
              </ul>
            </li>
          </ul>
        </nav>
      </div>
    </div>
  </div>
</template>
<script>

export default {
  data () {
    return {
      sideBarStore: [{
        name: 'system-setting',
        displayName: this.$t('nav.system_setting'),
        path: undefined,
        icon: 'fa fa-cog',
        showSubNav: true,
        subNav: [{
          name: 'latest-announcement',
          displayName: this.$t('nav.latest_announcement'),
          path: '/latest-announcement'
        }, {
          name: 'chat-record',
          displayName: this.$t('nav.chat_record'),
          path: '/chat-record'
        }]
      }, {
        name: 'chatroom-setting',
        displayName: this.$t('nav.chatroom_setting'),
        path: '/chatroom-setting',
        icon: 'fa fa-comments'
      }, {
        name: 'user-management',
        displayName: this.$t('nav.user_management'),
        path: undefined,
        icon: 'fa fa-users',
        showSubNav: true,
        subNav: [{
          name: 'member-list',
          displayName: this.$t('nav.member_list'),
          path: '/member-list'
        }, {
          name: 'agent-promo',
          displayName: this.$t('nav.agent_promo'),
          path: '/agent-promo'
        }, {
          name: 'robot-management',
          displayName: this.$t('nav.robot_management'),
          path: '/robot-management'
        }]
      }]
    }
  }
}
</script>



