<template>
  <div class="app-header white box-shadow" v-if="!$route.meta.hideNav">
    <div class="navbar-sm navbar-toggleable-sm flex-row align-items-center m-b-0">
      <!-- Open side - Naviation on mobile -->
      <a data-toggle="modal" data-target="#aside" class="hidden-lg-up mr-3 pull-left m-t-sm m-l-sm p-t-sm">
        <i class="material-icons">&#xe5d2;</i>
      </a>
      <!-- / -->
      <div class="h5 no-wrap pull-left m-t-sm m-l-sm p-t-sm" id="pageTitle">{{ $route.meta.title }}</div>

      <div class="btn-group dropdown pull-right m-t-sm m-r-sm">
        <button class="btn dropdown-toggle white" @click="$root.headerDropdown = !$root.headerDropdown">{{ $root.username }}</button>
        <div class="dropdown-menu pull-right" style="display: block;" v-show="$root.headerDropdown">
          <a class="dropdown-item">{{ $t('nav.change_password') }}</a>
          <div class="dropdown-divider"></div>
          <a class="dropdown-item" @click="logout">{{ $t('action.logout') }}</a>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import api from '../api'
export default {
  data () {
    return {}
  },
  methods: {
    logout () {
      this.$http.post(api.logout).then(
        data => {
          this.headerDropdown = false
          this.$router.push('/login')
          this.$cookie.delete('access_token')
          this.$cookie.delete('refresh_token')
        },
        error => {
          this.errorMsg = error
        }
      )
    }
  }
}
</script>

<style lang="scss" scoped>
.my {
  margin: 12px 0;
}
</style>



